<template>
  <div>
    <van-nav-bar title="报修处理（内修）" />
        <van-field v-model="fid" name="fid" style="display:none"/>
        <van-cell title="报修人:"  :value="frepair_person_name"  />
        <van-cell title="楼号:" v-if="frepair_build!=''" :value="frepair_build"  />
        <van-cell title="房间号:" v-if="room_num!=''" :value="room_num"  />
        <van-cell title="报修时间:" :value="frepair_date"  />
        <van-cell v-if="area!=null" title="区域:" :value="area"  />
        <van-cell title="报修类型:" :value="frepair_type"  />
        <van-cell title="故障描述:" :value="frservice_describe" />
        <van-button type="info" plain  block @click="getimg()"  style="width:90%;margin:10px auto">查看维修图片</van-button>
        <van-divider>选择物料</van-divider>
         <van-field
            name="remark"
            v-model="remark"
            rows="2"
            autosize
            label="备注"
            type="textarea"
            maxlength="100"
            placeholder="请输入"
            show-word-limit
        />
        <van-button  block type="info" @click="addmore()">添加新一行</van-button> 
        <van-list style="margin-bottom:44px;height:400px">
            <van-cell-group  v-for="(item, i) in table_list" :key="i">
                 <van-swipe-cell>
                <van-field  label="物料名称：" center :value="item.inames" readonly>
                    <template #button>
                         <van-button  size="small" type="info" @click="opendialog(i)">选择物料</van-button>
                    </template>
                </van-field>
                <van-field  label="物料编码：" :value="item.icodes" style="display:none" />
                <van-field label="物料数量：" v-model="item.inums"  placeholder="请输入数量" :value="item.inums" />
                    <template #right>
                    <van-button square text="删除" type="danger" style="height:100%"  @click="Delete(i)" />
                </template>
            </van-swipe-cell>
            </van-cell-group>  
        </van-list>
        <van-button  block type="info" @click="onSubmit()"  style="width:100%;position: fixed;bottom:0px;left:0px;">提交</van-button> 
    <van-dialog v-model="show" title="选择物料" show-cancel-button  :beforeClose="beforeClose">
        <van-search
            v-model="names"
            show-action
            placeholder="请输入搜索关键词"
            @search="goodsearch"
            >
            <template #action>
                <div @click="goodsearch">搜索</div>
            </template>
        </van-search>
        <van-field label="物料名称：" v-model="goods_name"  value="" readonly="" />
        <van-field label="物料价格：" v-model="goods_price"  value="" readonly=""/>
        <van-field label="仓库库存：" v-model="goods_nums"  value="" readonly=""/>
        <van-list
            style="height: 300px;overflow-y: scroll;"
            >
            <van-cell v-if="searchlist_show">无数据</van-cell>
            <van-cell v-for="item in datalist" :key="item.fid" :title="item.fgoods_name+'-'+item.fbrand" @click="appClick(item)" />
        </van-list>
    </van-dialog>
  </div>
</template>

<script>
import { ImagePreview } from 'vant';
import {apigoodssearch} from "@/api/index"
import {apioutsideinfo} from "@/api/index"
import {apiinsidesave} from "@/api/index"
import { Toast } from 'vant';
export default {
    name:'insideRepair',
    data(){
        return{
            fid:"",//待维修列表id
            frepair_type:"",//待维修列表渲染类型
            frepair_build:"",//待维修列表楼号
            frepair_person_name:"",//待维修报修人
            room_num:"",//待维修列表房间哈
            frepair_date:"",//待维修报修时间
            area:"",//待维修列表渲染区域
            frservice_describe:"",//待维修列表渲染故障描述
            img:[],//待维修列表渲染图片
            searchlist_show: true,//弹窗中列表无数据
            show:false,//弹窗的显示隐藏
            names:"",//弹窗中搜索值
            datalist:[],//搜索中搜索值
            goods_name:"",//弹窗中物料名称
            goods_code:"",//弹窗中隐藏的物料编码
            goods_price:"",//弹窗中隐藏的物料编码
            goods_nums:"",//弹窗中隐藏的物料编码
            table_list:[],
            data_down:'',
            remark:""
        
        }
    },
    methods:{
        // 图片预览
        getimg(){
            ImagePreview(this.img);
        },
        // 待维修信息渲染
        getrepairinfo(){
            var _this=this
            let params = {fid: this.fid}
            apioutsideinfo(params).then(res=>{
                var data=res.data.data[0]
                _this.frepair_type=data.frepair_type
                _this.frepair_build=data.frepair_build
                _this.frservice_describe=data.frservice_describe
                _this.area=data.area
                _this.frepair_person_name=data.frepair_person_name
                _this.room_num=data.room_num
                _this.frepair_date=data.frepair_date
                _this.img=data.url_list_array
            })
        },
        // 提交
        onSubmit() {
            this.$loading('提交中');
            let data={
                fid:this.fid,
                goodlist:this.table_list
            }
            apiinsidesave(data).then(res=>{
                if(res.data.code==200){
                    this.$loading.close();
                    Toast.success(res.data.msg);
                    setTimeout(() =>{
                        this.$router.push({
                            path:"/waitrepairlist"
                        });
                    },3000);
                }else{
                    this.$loading.close();
                    Toast.success(res.data.msg);
                }     
            })
        },
        // 物料名称搜索
        goodsearch(){
            let params={
                fid:this.fid,
                fgoods_name:this.names
            }
            apigoodssearch(params).then(res=>{
                this.datalist=res.data.data
                if(res.data.data!=""){
                    this.searchlist_show=false
                }else{
                    this.searchlist_show=true
                }
                
            })
        },
        // 搜索选中
        appClick(data){
            console.log(data)
            this.goods_name=data.fgoods_name+'-'+data.fbrand
            this.goods_code=data.fid
            this.goods_price=data.fprice+"元"
            this.goods_nums=data.fstock+data.funit
        },
        //打开弹窗
        opendialog(i){
            this.names="",
            this.goods_name="",
            this.goods_code="",
            this.show=true
            this.goodsearch();
            this.data_down=i
        },
        beforeClose(action, done) {
            if(action === 'confirm') {
                this.table_list[this.data_down].inames=this.goods_name,
                this.table_list[this.data_down].icodes=this.goods_code,
                done() //不关闭弹框
            } else if(action === 'cancel') {
                done() //关闭
            }
        },
        addmore(){
            let obj={
                inames:"",
                icodes:"",
                inums:"",
            }
            this.table_list.push(obj)
        },
        Delete(index) {
            this.table_list.splice(index, 1)
            for (let i in this.table_list) {
                this.table_list[i].index = i
            }
        }
    },
    created(){
        this.fid=this.$route.query.fid
        this.getrepairinfo();
        this.goodsearch();
    }
}
</script>

<style>
.van-cell__value{
    flex: 2;
}
body{
    height: 100%;
}

</style>


